
.nickname{font-family:"microsoft yahei";position:absolute;width:100%;height:100%;background:#ededed;z-index:9999
}
.nickname_header{width:100%;position:fixed;top:0;left:0;z-index:9999
}
.nickname_content{position:relative;top:1.17333333rem
}
.nickname_content .weui-cells .weui-cell{padding:0 .37333333rem !important;height:1.17333333rem !important;font-size:.37333333rem;color:#3d4245
}
.nickname_content .weui-cells .weui-cell:active{background-color:#fff !important
}
.nickname_content .weui-cells .weui-cell .weui-cell__bd{padding:0 !important;width:100%;height:100%
}
.nickname_content .weui-cells .weui-cell .weui-cell__bd input{width:100%;height:100%
}
.nickname_content .preserve{width:9.04rem;height:1.17333333rem;margin:.72rem 0 0 .48rem;border:1px solid #009ff2;border-radius:.13333333rem;background:#fff;color:#009ff2;font-size:.4rem;font-weight:500
}
.nickname_content .preserve:active{color:#fff;background:#009ff2
}
.move-transition{-webkit-transition:all .2s linear;transition:all .2s linear;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)
}
.move-enter,.move-leave{-webkit-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0)
}


